	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Тяга горизонтального блока задействует те же мышцы, что и упражнение тяга штанги в наклоне, и обладает положительными особенностями: снимается нагрузка с коленей и позвоночника. Для выполнения тяги понадобится нижний блок с прикрепленной к нему V-образной рукояткой – обычно используют именно её, но также можно прикреплять любые другие рукоятки, от них зависит на какие мышцы больше пойдет нагрузка:</p>\n<ul>\n  <li>чем уже хват, тем сильнее работает бицепс, но увеличивается амплитуда, что позволяет сильнее растягивать мышцы спины</li>\n  <li>широкий хват напротив – переносит нагрузку на спину, но сокращает амплитуду упражнения</li>\n</ul>\n<h3>Порядок выполнения:</h3>\n<ol>\n  <li>Сядьте на тренажер, упритесь ногами в подставку. При выполнении упражнения спину держите прямой.</li>\n  <li>Исходное положение: слегка согните ноги в коленях, возьмите рукоятку, отклоняйтесь назад, пока туловище не будет перпендикулярно ногам, подайте грудь вперед.</li>\n  <li>На выдохе - держа туловище неподвижно, тяните рукоятку на себя до тех пор, пока кисти не коснутся брюшного пресса. Задержитесь на пару секунд в этом положении, напрягая мышцы спины.</li>\n  <li>Не отклоняйтесь назад во время тяги на нижнем блоке – это включает в работу второстепенные мышцы, что снижает эффективность упражнения.</li>\n</ol>\n<h3>Советы:</h3>\n<ul>\n  <li>Подбирайте такой вес, чтобы могли выполнить 6 – 8 повторений.</li>\n  <li>В крайней точке упражнения (ручка притянута к животу), спина должна быть прямой, а лопатки сведены вместе.</li>\n  <li>Когда вы отпускаете ручку от себя, подайтесь вперед, не сдвигая ягодицы и держа спину прямо - это позволит сильнее растянуть мышцы спины.</li>\n  <li>На вдохе - медленно выпрямляйте руки, и немного подайте корпус вперед. Руки при этом не выпрямляйте до конца, чтобы более эффективно нагрузить спину.</li>\n  <li>Выполните требуемое количество повторений.</li>\n</ul>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/IzoCF_b3cIY\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/tyaga-gorizontalnogo-bloka","efficiency":"7","title":"Тяга горизонтального блока","image":"https://iq-body.ru/uploads/ra/755x567/training/original/горблок1.jpg","image_2":"https://iq-body.ru/uploads/ra/755x567/training/original/горблок2.jpg","image_3":"","image_4":"","filterby":"back","date":"April 12, 2017","injury":"3","bodypart":"Спина","filtertype":"isolated","type":"Изолирующее","filterinventory":"blocktrenajer","inventory":"Блоки","filterplace":null}}},"pathContext":{}}

